import {ArrayBuffer} from "core-js/internals/array-buffer";
const docxPreview = require('docx-preview')
const mammoth = require('./lib/mammoth.browser')

function getData(src, options) {
    if (typeof src === 'string') {
         return fetchDocx(src, options)
    }
    return Promise.resolve(src)
}

function fetchDocx(src, options={}) {
    return fetch(src, options)
}

function render(renderType, data, container){
    if(renderType === 'html'){
        return renderHtml(data, container)
    }else if(renderType === 'canvas'){
        return renderCanvas(data, container)
    }
}

function renderHtml(data, container){
    if(!data){
        container.innerHtml = ''
        return
    }
    let arrayBuffer
    if(data instanceof ArrayBuffer){
        arrayBuffer = data
    }else if(data instanceof Response) {
        arrayBuffer = data.arrayBuffer()
    }

    return mammoth.convertToHtml({arrayBuffer})
        .then(function (result) {
            container.innerHTML = result.value
        });
}

function renderCanvas(data, container){
    if(!data){
        container.innerHtml = ''
        return
    }
    let blob
    if(data instanceof Blob){
        blob = data
    }else if(data instanceof Response) {
        blob = data.blob()
    }
    return docxPreview.renderAsync(blob, container);
}

export default {
    getData,
    render
}