<template>
  <div class="vue-office-docx" ref="vue-office-docx"></div>
</template>

<script>
import docx from './docx'

export default {
  name: "VueOfficeDocx",
  props: {
    src: [String, ArrayBuffer],
    renderType:{
      type: String,
      default: 'html'
    }
  },
  data() {
    return {
      htmlData: ''
    }
  },
  watch: {
    src: {
      handler(val) {
        if (val) {
          this.init()
        }else{
          docx.render(this.renderType, '', this.$refs["vue-office-docx"])
        }
      }
    }
  },
  mounted() {
    if(this.src){
      this.init()
    }
  },
  methods: {
    init(){
      docx.getData(this.src).then(res =>{
        console.log(res)
        docx.render(this.renderType, res, this.$refs["vue-office-docx"])
      })
    }
  }
}
</script>

